.shop_plug {
  width: 265px;
  margin: 120px auto;
  display: none;
}

.shop_plug-title {
  font-size: 14px;
  font-weight: inherit;
  text-align: center;
  letter-spacing: -.02em;
  color: var(--black-60);
}

.shop_plug-img {
  width: 160px;
  height: 142px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.shop_card {
  height: 175px;
  border: 2px solid var(--violet-40);
  background-color: var(--white-60);
  border-radius: 16px;
  margin-bottom: 20px;
  padding: 14px;
  display: flex;
}

.shop_card-img {
  object-fit: cover;
  width: 100px;
  height: 142px;
  border-radius: 8px;
}

@media screen and (min-width: 768px) {
  .shop_card-img {
    width: 110px;
    height: 165px;
  }
}

.shop_card-div_description {
  width: 100%;
  flex-direction: column;
  margin-left: 14px;
  display: flex;
}

.shop_card-title {
  text-transform: uppercase;
  color: var(--balck);
  font-size: 16px;
  line-height: 18px;
}

.shop_card-category {
  font-weight: inherit;
  letter-spacing: .03em;
  color: var(--sub-text-color);
  margin-top: 4px;
  font-size: 12px;
  line-height: 14px;
}

.shop_card button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("del.74feb1ea.png");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
}

.shop_card-div_title {
  justify-content: space-between;
  display: flex;
}

.shop_card-description {
  text-overflow: ellipsis;
  letter-spacing: -.04em;
  color: var(--black);
  margin-top: 8px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 129%;
  display: inline-block;
  overflow-y: auto;
}

.shop_card-description::-webkit-scrollbar {
  display: none;
}

@media screen and (min-width: 768px) {
  .shop_card-description {
    width: 100%;
    height: fit-content;
    overflow: visible;
  }
}

.shop_card-empty_place {
  flex-grow: 1;
}

.shop_card-footer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.shop_card-author {
  font-style: italic;
  font-size: var(--sl-font-size_desc);
  color: var(--sub-text-color);
  line-height: 14px;
}

.shop_card-link {
  margin-left: 8px;
}

.shop_card-link img {
  height: 16px;
}

.shop_card-link img:hover {
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, .15));
}

.shop_card-link .shop_amazon--black, .shop_card-link .shop_amazon--wite {
  height: 11px;
}

@media screen and (min-width: 768px) {
  .shop_plug {
    width: 322px;
    margin: 140px auto;
  }

  .shop_title {
    font-size: var(--font-size_tablet_PC-title);
    line-height: 52px;
  }

  .shop_plug-title {
    font-size: var(--font-size_tablet_PC-plug-title);
    line-height: 24px;
  }

  .shop_card-div_description {
    margin-left: 24px;
  }

  .shop_card {
    height: 220px;
    padding: 24px;
  }

  .shop_card button {
    width: 34px;
    height: 34px;
  }

  .shop_card-description {
    margin-top: 14px;
  }

  .shop_card-link {
    margin-left: 16px;
  }

  .shop_card-link img {
    height: 28px;
  }

  .shop_card-link .shop_amazon--black, .shop_card-link .shop_amazon--wite {
    height: 16px;
  }
}

.asides {
  display: none;
}

@media screen and (min-width: 1440px) {
  .asides {
    display: block;
  }
}

